.gallery {
	background-color: $color-grey-light-1;
	grid-column: full-start / full-end;

	display: grid;
	grid-template-columns: repeat(8, 1fr);
	grid-template-rows: repeat(7, 5vw);
	grid-gap: 1.5rem;
	padding: 1.5rem;

	&__item {
		&__1 {
			grid-row: 1 / span 2;
			grid-column: 1 / span 2;
		}
		&__2 {
			grid-row: 1 / span 3;
			grid-column: 3 / span 3;
		}
		&__3 {
			grid-row: 1 / span 2;
			grid-column: 6 / span 1;
		}
		&__4 {
			grid-row: 1 / span 2;
			grid-column: 7 / span 2;
		}
		&__5 {
			grid-row: 3 / span 3;
			grid-column: 1 / span 2;
		}
		&__6 {
			grid-row: 4 / span 2;
			grid-column: 3 / span 2;
		}
		&__7 {
			grid-row: 4 / span 1;
			grid-column: 5 / span 1;
		}
		&__8 {
			grid-row: 3 / span 2;
			grid-column: 6 / span 2;
		}
		&__9 {
			grid-row: 3 / span 3;
			grid-column: 8 / span 1;
		}
		&__10 {
			grid-row: 6 / span 2;
			grid-column: 1 / span 1;
		}
		&__11 {
			grid-row: 6 / span 2;
			grid-column: 2 / span 2;
		}
		&__12 {
			grid-row: 6 / span 2;
			grid-column: 4 / span 1;
		}
		&__13 {
			grid-row: 5 / span 3;
			grid-column: 5 / span 3;
		}
		&__14 {
			grid-row: 6 / span 2;
			grid-column: 8 / span 1;
		}
	}

	&__img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
